@font-face {
  font-family: "Bubble Bobble";
  src: local("Bubble Bobble"),
   url("./fonts/BubbleBobble-rg3rx.ttf") format("truetype");
   font-weight: normal;
  }

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
