.lessonlist-container {
  background-image: url("../img/bg_study_1.jpg");
  background-repeat: no-repeat;
  background-size: cover; 
  text-align: center;
  font-family: "Bubble Bobble";
  height: 968px;
  width: 1920px;
}
.question {
    position: relative;
    display: flex;
    flex-direction: column;
}


.comment.unhide {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.comment.hide{
  display: none!important;
}


.card-content1{
  cursor: pointer;
  background-color: white;
  border-radius: 5%;
  width: 540px;
  height: 630px;
  display: flex;
  position: relative;
  flex-direction: column;
  margin-top: 10px;
  box-shadow: 5px 10px 18px gray;
}
.card-content1:hover{
  transform: scale(105%);
  transition: 0.5s ease-in-out;
}
.card-content2{
  border: 3px solid red;
  border-radius: 5%;
  position: absolute;
  width: 480px;
  height: 585px;
  margin: 20px auto auto 20px;
  margin-left: 30px;
}
.question-text2{
  font-size: 97px;
  color: red;
  margin-top: -23px;
}
.question-text1{
  font-size: 90px;
  color: red;
  padding-left: 23px;
  
}
.question-volume {
  margin-right: 23px;
  margin-top: 23px;


}
.question-img{
  width: 435px ;
  height: 375px;
  margin-top: -30px;
}

.option{
  cursor: pointer;
  margin-top: 60px;
  margin-bottom: 15px;
  border-radius: 15%/70%;
  font-size: 120px;
  color: white;
  width: 720px;
  height: 180px;
  box-shadow:
        inset 0 0 40px 0 rgba(255,255,255,.4),
        inset 0 0 55px 0 rgba(0,0,0,.4),
        inset 0 0 65px 5px rgba(0,0,0,.05),
        6px 6px 9px 0 rgba(0,0,0,.25);

}

.option:hover{
  transform: scale(110%);
  transition: 0.5s;
}

.option.left-color{
  background-color: red;
}
.option.right-color{
  background-color: green;
}